import React from "react";
import { FaHouseUser,FaStar,FaWallet,FaEllipsisH } from "react-icons/fa";

const Footer = () => {
    return (
        <div class="footer">
            <div className="home" id="home">
                <FaHouseUser className='house'/>

                <p>Home</p>
            </div>
            <div className="mymatches">
                <FaStar className='star'/>

                <p>MyMatches</p>
            </div>
            <div className="wallet">
                <FaWallet className='walt'/>

                <p>wallet</p>
            </div>
            <div className="More">
                <FaEllipsisH className='ellipsis'/>

                <p>More</p>
            </div>
        </div>
    );
}
export default Footer;